window.dom = {
  /**
   * åˆ›å»ºèŠ‚ç‚¹
   * @param {String} str åˆ›å»ºçš„èŠ‚ç‚¹ï¼Œeg: <div>1<div> 
   * @returns 
   */
  create(str){
    const container = document.createElement("template");
    container.innerHTML = str.trim();
    return container.content.firstChild;
  },
  /**
   * åœ¨OldNodeä¹‹åŽæ’å…¥newNode
   * @param {node} oldNode 
   * @param {node} newNode 
   */
  after(oldNode, newNode){
    oldNode.parentNode.insertBefore(newNode, oldNode.nextSibling);
  },
  /**
   * åœ¨oldNodeä¹‹å‰æ’å…¥newNode
   * @param {node} oldNode 
   * @param {node} newNode 
   */
  before(oldNode, newNode){
    oldNode.parentNode.insertBefore(newNode, oldNode);
  },
  /**
   * å­èŠ‚ç‚¹childæ’å…¥çˆ¶èŠ‚ç‚¹parent
   * @param {node} parent çˆ¶èŠ‚ç‚¹
   * @param {node} child å­èŠ‚ç‚¹
   */
  append(parent, child){
    parent.appendChild(child)
  },
  /**
   * ç»™oldNode æ·»åŠ çˆ¶èŠ‚ç‚¹
   * @param {node} oldNode åŽŸèŠ‚ç‚¹
   * @param {node} newParent æ–°çš„çˆ¶èŠ‚ç‚¹
   */
  wrap(oldNode, newParent){
    dom.before(oldNode, newParent);
    dom.append(newParent, oldNode);
  },
  /**
   * åˆ é™¤ä¸€ä¸ªnode
   * @param {node} node éœ€è¦åˆ é™¤çš„node
   * @returns è¿”å›žåˆ é™¤çš„node
   */
  remove(node){
    node.parentNode.removeChild(node);
    return node;
  },
  /**
   * æ¸…ç©ºå­èŠ‚ç‚¹
   * @param {node} node åˆ é™¤å­èŠ‚ç‚¹
   * @returns è¿”å›žæ‰€æœ‰å­èŠ‚ç‚¹ï¼Œä¸€å±‚çš„
   */
  empty(node){
    const array = [];
    let x = node.firstChild;
    while(x){
      array.push(dom.remove(node.firstChild));
      x = node.firstChild;
    }
    return array
  },
  /**
   * 
   * @param {node} node æ·»åŠ \èŽ·å–å±žæ€§çš„èŠ‚ç‚¹
   * @param {string} name å±žæ€§å
   * @param {string} value å±žæ€§å€¼ 
   * @returns 
   */
  attr(node, name, value){
    if(arguments.length === 3){
      node.setAttribute(name, value);
    }else if(arguments.length === 2) {
      return node.getAttribute(name);
    }
  },
  /**
   * æ’å…¥æˆ–æŸ¥è¯¢èŠ‚ç‚¹çš„æ–‡æœ¬å†…å®¹
   * @param {node} node èŠ‚ç‚¹
   * @param {string} string æ’å…¥çš„å†…å®¹
   * @returns å½“åªæœ‰ä¸€ä¸ªå…¥å‚è¿”å›žèŠ‚ç‚¹çš„æ–‡æœ¬å†…å®¹
   */
  text(node, string){
    if(arguments.length === 2){
      if('innerText' in node){
        node.innerText = string;
      }else {
        node.textContent = string;
      }
    }else if(arguments.length === 1){
      if('innerText' in node){
        return node.innerText;
      }else {
        return node.textContent;
      }
    }
  },
  /**
   * å‘èŠ‚ç‚¹æ’å…¥html
   * @param {node} node èŠ‚ç‚¹
   * @param {string} string æ’å…¥çš„htmlå†…å®¹ 
   * @returns å½“åªæœ‰ä¸€ä¸ªå…¥å‚ï¼Œè¿”å›žèŠ‚ç‚¹çš„html
   */
  html(node, string){
    if(arguments.length === 2){
      node.innerHTML = string;
    }else if(arguments.length === 1){
      return node.innerHTML;
    }
  },
  /**
   * æŸ¥çœ‹æˆ–è®¾ç½®èŠ‚ç‚¹æ ·å¼
   * @param {node} node èŠ‚ç‚¹
   * @param {string|Object} name æ ·å¼åæˆ–æ ·å¼å¯¹è±¡
   * @param {string} value æ ·å¼å€¼
   * @returns å½“å…¥å‚æ˜¯ä¸¤ä¸ªä¸”ç¬¬äºŒä¸ªå‚æ•°æ˜¯stringç±»åž‹ï¼Œè¿”å›žè¯¥æ ·å¼åçš„å±žæ€§å€¼
   */
  style(node, name, value){
    if(arguments.length === 3){
      node.style[name] = value;
    }else if(arguments.length === 2){
      if(typeof name === 'string') {
        return node.style[name];
      }else if(node instanceof Object){
        const object = name;
        for(let key in objcet){
          node.style[key] = object[key]
        }
      }
    }
  },
  class: {
    /**å¢žåŠ class */
    add(node, className){
      node.classList.add(className);
    },
    /**ç§»é™¤class */
    remove(node, className){
      node.classList.remove(className);
    },
    /**æŸ¥çœ‹æ˜¯å¦æœ‰class */
    has(node, className){
      return node.classList.contains(className);
    }
  },
  on(node, eventName, fn){
    node.addEventListener(eventName, fn);
  },
  off(node, eventName, fn){
    node.removeEventListener(eventName, fn);
  },
  find(selector, scope){
    return (scope || document).querySelectorAll(selector);
  },
  parent(node){
    return node.parentNode;
  },
  children(node){
    return node.children;
  },
  siblings(node){
    return Array.from(node.parentNode.children).filter(n => n!==node)
  },
  next(node){
    let x = node.nextSibling;
    while(x && x.nodeType === 3){
      x = x.nextSibling;
    }
    return x;
  },
  previous(node){
    let x = node.previousSibling;
    while(x && x.nodeType === 3){
      x = x.previousSibling;
    } 
    return x;
  },
  each(nodeList, fn){
    for(let i=0;i<nodeList.length;i++){
      fn.call(null, nodeList[i]);
    }
  },
  index(node){
    const list = dom.children(node.parentNode);
    let i;
    for(i=0;i<list.length;i++){
      if(list[i] === node){
        break;
      }
    }
    return i;
  }
}
